import React, { useCallback, useEffect, useRef, useState } from 'react'
import { Link } from 'gatsby'
import { useFormikContext } from 'formik';
import { FiArrowLeft } from "react-icons/fi";
import { IconButton, useToken, Icon, AspectRatio } from '@chakra-ui/react'
import { max } from 'lodash';
import { StaticImage } from 'gatsby-plugin-image';
import { motion } from 'framer-motion';

import Image from '../components/Image'
import Box from '../components/Box'
import Text from '../components/Text'
import Flex from '../components/Flex'
import Button from '../components/Button'
import BackgroundImage from '../components/BackgroundImage'
import { responsive } from '../components/ThemeProvider/theme';

import { trackAnswer } from '../utils/trackEvent';
import score from '../json/score';
import PaintLink from '../components/PaintLink';

export const images = [
  [require('../image/q1.webp').default, require('../image/q1.png').default],
  [require('../image/q2.webp').default, require('../image/q2.png').default],
  [require('../image/q3.webp').default, require('../image/q3.png').default],
  [require('../image/q4.webp').default, require('../image/q4.png').default],
  [require('../image/q5.webp').default, require('../image/q5.png').default],
  [require('../image/q6.webp').default, require('../image/q6.png').default],
]

const layout = 'fullWidth'
const placeHolder = 'dominantColor'
const loading = 'lazy'

const picture = {
  1: <StaticImage alt="Q1" src="../image/q1.png" layout={layout} loading={loading} placeholder={placeHolder} />,
  2: <AspectRatio ratio={768 / 599}><Image alt="Q2" src={images[1]} /></AspectRatio>,
  3: <AspectRatio ratio={768 / 599}><Image alt="Q3" src={images[2]} /></AspectRatio>,
  4: <AspectRatio ratio={768 / 599}><Image alt="Q4" src={images[3]} /></AspectRatio>,
  5: <AspectRatio ratio={768 / 599}><Image alt="Q5" src={images[4]} /></AspectRatio>,
  6: <AspectRatio ratio={768 / 599}><Image alt="Q6" src={images[5]} /></AspectRatio>,
}

// const picture = {
//   1: <StaticImage alt="Q1" src="../image/q1.png" layout={layout} loading={loading} placeholder={placeHolder} />,
//   2: <StaticImage alt="Q2" src="../image/q2.png" layout={layout} loading={loading} placeholder={placeHolder} />,
//   3: <StaticImage alt="Q3" src="../image/q3.png" layout={layout} loading={loading} placeholder={placeHolder} />,
//   4: <StaticImage alt="Q4" src="../image/q4.png" layout={layout} loading={loading} placeholder={placeHolder} />,
//   5: <StaticImage alt="Q5" src="../image/q5.png" layout={layout} loading={loading} placeholder={placeHolder} />,
//   6: <StaticImage alt="Q6" src="../image/q6.png" layout={layout} loading={loading} placeholder={placeHolder} />,
// }

const calculateScore = Object.values(score)
const citys = Object.keys(score)

const Question = ({ data, pageContext: { id, isLast, length }, onNext, onPrev }) => {
  const [isSubmitting, setSubmitting] = useState()
  const [resCity, setCity] = useState()
  const [teal] = useToken('colors', ['custom.teal'])
  const { values, setFieldValue } = useFormikContext()
  const resLinkRef = useRef()
  const handleSubmit = useCallback((lastAnswer) => {
    const allAnswers = values.answers.slice(0, 5).concat(lastAnswer)
    setSubmitting(true)
    const totolPoints = calculateScore.reduce((res, scores) => {
      const point = scores.reduce((total, num, i) => {
        total = total + (num[allAnswers[i]] || +'0')
        return total
      }, 0)
      res.push(point)
      return res
    },[])
    const cityIndexs = totolPoints.reduce((res, num, i) => {
      if (num === max(totolPoints)) {
        res.push(i)
      }
      return res
    }, [])
    const city = citys[cityIndexs[Math.floor(Math.random()*cityIndexs.length)]]
    setFieldValue('points', max(totolPoints))
    setCity(city)
  }, [values, setFieldValue])
  useEffect(() => {
    if (resCity) {
      setTimeout(() => {
        if (resLinkRef.current) {
          resLinkRef.current.querySelector('a').click()
        }
      })
    }
  }, [resCity])
  return (
    <>
      <Flex alignItems="flex-end" justifyContent="space-around">
        <Box width={responsive('100%', '442px')}>
          <Flex
            pb="0.25em"
            alignItems="center"
            justify="space-between"
            borderBottom="1px solid"
            borderColor="custom.borderGray"
          >
            <IconButton
              as={id === '1' && (p => <Link to="/" {...p} />)}
              w="1.5em"
              h="1.5em"
              mr="0.25em"
              minWidth="auto"
              bg="transparent"
              color="custom.darkBlue"
              icon={<Icon as={FiArrowLeft} w={6} h={6} />}
              isDisabled={isSubmitting}
              _hover={{
                bg: 'transparent'
              }}
              onClick={() => {
                if (id !== '1') {
                  onPrev()
                }
              }}
            />
            <Text
              fontSize="1.5rem"
              letterSpacing="0.125em"
              fontFamily="Flama"
              color="lightGray"
            >
              <Text.Inline color="gray">{id}</Text.Inline>/{length}
            </Text>
          </Flex>
          <Text
            mt="0.5em"
            fontWeight="500"
            color="custom.darkBlue"
            fontSize="1.125em"
          >{data.question}</Text>
          <Box pt={responsive('1em', '3em')} px="7.5%">
            {picture[id]}
          </Box>
          <Box>
            {data.options.map((opt, i) => (
              <Box
                as={motion.div}
                whileTap={{ scale: 1.1 }}
                mt={i && '1em'}
                key={i}
              >
                <Button
                  // as={!isLast && ((p) => <Link {...p} to={`/q/${+id + 1}`} />)}
                  onClick={(e) => {
                    if (!isLast) {
                      onNext()
                    }
                    trackAnswer(id, i)
                    setFieldValue(`answers.${id - 1}`, i)
                    if (isLast) {
                      handleSubmit(i)
                    }
                  }}
                  width="100%"
                  isDisabled={isSubmitting}
                  isActive={values.answers[+id - 1] === i}
                  // bg={values.answers[+id - 1] === i ? 'custom.teal' : 'custom.gray'}
                  _hover={{
                    // bg: id === '1' ? responsive('custom.gray', 'custom.teal') : 'custom.teal',
                    bg: responsive('custom.gray', 'custom.teal'),
                  }}
                  _active={{
                    bg: 'custom.teal',
                  }}
                >
                  {opt}
                </Button>
              </Box>
            ))}
          </Box>
        </Box>
      </Flex>
      {isLast && (
        <Box.Absolute opacity="0" width="0" height="0" overflow="hidden" ref={resLinkRef}>
          <PaintLink tabIndex="-1" hex={teal} to={`/result/${resCity}`} />
        </Box.Absolute>
      )}
    </>
  )
}

export default Question

