import { graphql, navigate, withPrefix } from 'gatsby'
import React, { useCallback, useEffect, useState, useRef, useMemo } from 'react'
// import Slider from 'react-slick';
// import 'slick-carousel/slick/slick.css';
// import 'slick-carousel/slick/slick-theme.css';
import { Box } from '@chakra-ui/react';
import { useFormikContext } from 'formik';
import styled from '@emotion/styled';

import Layout from '../containers/Layout'
import Question, { images } from './question'
import Container from '../components/Container';
import { responsive } from '../components/ThemeProvider/theme';
import imageLoader from '../containers/imageLoader'
import { useSessionStorage } from 'react-use';

const StyledLayout = styled(Layout)`
@media (min-height: 680px) and (min-width: 784px) {
  .footer {
    padding-top: 0px;
    padding-bottom: 0px;
    position: absolute;
    transform: translateX(-50%);
    background: transparent;
  }
}
@media (min-height: 811px) and (min-width: 784px) {
  height: 812px;
}
@media (min-height: 680px) and (max-width: 784px) {
  .main-wrapper {
    height: calc(100% - 57.25px);
  }
}
@media (max-height: 679px) and (min-width: 784px) {
  .main-wrapper {
    height: auto;
  }
}
`

const Q = ({ data, pageContext }) => {
  const { id } = pageContext
  const { length } = data.allQuestionsJson.nodes
  const curentId = id - 1
  const { values } = useFormikContext()
  const [value] = useSessionStorage('view-home');
  const handleNext = useCallback((i) => {
    setTimeout(() => {
      // slickRef.current.slickGoTo(i + 1)
      navigate(`/q/${i + 2}`)
    }, 300)
  }, [])
  const handlePrev = useCallback((i) => {
    // slickRef.current.slickGoTo(i - 1)
    navigate(`/q/${i}`)
  }, [])
  useEffect(() => {
    if (id < 6) {
      imageLoader([images[+id]])
    }
  }, [id])
  const answerNotMatch = useMemo(() => values.answers.length < id - 1, [id, values.answers.length])
  // useEffect(() => {
  //   if (loaded.current) {
  //     const pathname = withPrefix(`/q/${curentId + 1}`)
  //     window.history.pushState(null, null, pathname)
  //     if (window.gtag) {
  //       window.gtag(`event`, `page_view`, { page_path: pathname })
  //     }
  //   }
  //   loaded.current = true
  // }, [curentId])

  useEffect(() => {
    if (answerNotMatch || !value) {
      if (/\/q\//.test(window.location.pathname)) {
        navigate('/')
      }
    }
  }, [answerNotMatch])

  const contents = useMemo(() => (
    data.allQuestionsJson.nodes.map((d, i) => i === curentId && (
      <Box bg="white" key={d.id} px="1.875em">
        <Question
          data={d}
          onNext={() => handleNext(i)}
          onPrev={() => handlePrev(i)}
          pageContext={{ id: d.id, length, isLast: i === length - 1 }}
        />
      </Box>
    ))
  ), [curentId, data.allQuestionsJson.nodes, handleNext, handlePrev, length])
  if (answerNotMatch || !value) return null

  return (
    <StyledLayout
      bg="white"
      title={`第${curentId + 1}題`}
      footerProps={{
        // bg: responsive('transparent', height > 630 ? 'transparent' : 'white'),
        maxWidth: responsive('auto', '375px'),
        mx: 'auto',
        py: '1em',
        position: 'static',
        left: '50%',
        transform: 'none',
        bottom: '2em',
      }}
      headerProps={{
        mt: "1.5em"
      }}
    >
      <Container className="main-wrapper" height={responsive('auto', '100%')} maxWidth={responsive('auto', '375px')} pt="1em" px="0">
        {contents}
      </Container>
    </StyledLayout>
  )
}

export default Q

export const query = graphql`
  query QuestionPageQuery {
    allQuestionsJson {
      nodes {
        id
        question
        options
      }
    }
  }
`
